import React, {Component} from 'react';
import {BrowserRouter as Router, Route, Link, Redirect, withRouter} from "react-router-dom";
import Cookies from 'universal-cookie';
import demo from './demo/demo';
import Login from './login/login';
import App from './main/mainPage'
// import fakeAuth from './login/fakeAuth';


const cookies = new Cookies();

const PrivateRoute = ({ component: Component, ...rest }) => (
<Route
        {...rest}
        render={props =>
            cookies.get('user') !== "" ? (
                <Component {...props} />
            ) : (
                <Redirect
                    to={{
                        pathname: "/",
                        state: { from: props.location }
                    }}
                />
            )
        }
    />
);

const VolatileRoute = ({ component: Component, ...rest }) => (
    <Route
        {...rest}
        render={props =>
            cookies.get('user') === "" ? (
                <Component {...props} />
            ) : (
                <Redirect
                    to={{
                        pathname: "/main",
                        state: { from: props.location }
                    }}
                />
            )
        }
    />
);


function loadJS(src) {
    var ref = window.document.getElementsByTagName("script")[0];
    var script = window.document.createElement("script");
    script.src = src;
    script.async = true;
    ref.parentNode.insertBefore(script, ref);
}


class Routes extends Component {
    // componentDidMount(){
    //     loadJS('https://maps.googleapis.com/maps/api/js?key=AIzaSyCilGEk3edqzDS5XSnBGGwOhIEamO2RN9o');
    // }

    render() {
        return (
            <Router>
                <div>
                    <VolatileRoute exact path="/" component={Login}/>
                    <Route path="/demo" component={demo} />
                    <PrivateRoute path="/main" component={App} />
                    <Route path="/signup" component={Login} />
                    <Route path="/resetpw" component={Login} />
                </div>
            </Router>
        );
    }
}

export default Routes;



