import React, { Component } from 'react';
import {Button, nav} from 'react-materialize';
class Show extends Component {
    state = {users: []};
    componentDidMount() {
        fetch('/users')
            .then(res => res.json())
            .then(users => this.setState({ users }));
    }

    render() {
        return (
            <div class="container">
                <table>
                    <thead>
                    <tr>
                        <th>id</th>
                        <th>name</th>
                        <th>city</th>
                        <th>state</th>
                        <th>latitude</th>
                        <th>longitude</th>
                    </tr>
                    </thead>
                    <tbody>
                    {this.state.users.map(user =>
                        <tr key={user.id}>
                            <td>{user.id}</td>
                            <td>{user.name}</td>
                            <td>{user.city}</td>
                            <td>{user.state}</td>
                            <td>{user.latitude}</td>
                            <td>{user.longitude}</td>
                        </tr>
                    )}
                    </tbody>
                </table>
            </div>
        );
    }
}
export default Show;


