import React, { Component } from 'react';
import {Button, nav} from 'react-materialize';
import { BrowserRouter as Router, Route, Link} from "react-router-dom";
import show from './show';
import insert from './insert';
import update from './update';
import del from './delete';
import busList from '../main/busList';

class Demo extends Component{

    render() {
        return (
            <Router>
                <div id="wrapper" >
                    <nav>
                        <div class="nav-wrapper container">
                            <Link to="/demo" class="brand-logo">Database Demo</Link>
                            <ul id="nav-mobile" class="right hide-on-med-and-down">
                                <li><Link to="/demo/show">Show</Link></li>
                                <li><Link to="/demo/insert">Insert</Link></li>
                                <li><Link to="/demo/update">Update</Link></li>
                                <li><Link to="/demo/delete">Delete</Link></li>
                            </ul>
                        </div>
                    </nav>
                    <Route exact path="/demo/" component={show} />
                    <Route path="/demo/show" component={show} />
                    <Route path="/demo/bus" component={busList} />
                    <Route path="/demo/insert" component={insert}/>
                    <Route path="/demo/update" component={update}/>
                    <Route path="/demo/delete" component={del}/>

                </div>
            </Router>
        );
    }
}

export default Demo;


