import React, { Component } from 'react';
import {Row, Input, Button, nav} from 'react-materialize';
import { BrowserRouter as Router, Route, Link, Redirect, withRouter} from "react-router-dom";
import Cookies from 'universal-cookie';
// import fakeAuth from './fakeAuth';
var divStyle = {
    display: "inline-block",
    padding: "32px 48px 0px 48px",
    border: "1px solid #EEE",
    height: "355px",
    width: "349px"
};




class Signon extends Component {
    constructor(props) {
        super(props);
        this.state = {username: "", password: "",  redirectToReferrer: false};
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.login = this.login.bind(this);
        // this.inputPW = React.cr;
    }

    handleChange(event) {
        switch (event.target.id) {
            case "inputUN":
                this.state.username = event.target.value;
                break;
            case "inputPW":
                this.state.password = event.target.value;
                break;
        }
    }

    handleSubmit(event) {
        console.log(this.state);
        fetch("/credentials/verify", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify(this.state)
        }).then(res => res.json())
            .then(data => {
                console.log(data[0].correct);
                if (!data[0].correct) {
                    alert("Username and password does not match, please try again");
                    console.log(this.inputPW.value);
                    // this.inputPW.value = "";
                } else {
                    alert("Congrats!");
                    this.login(data[0].userid);
                    window.location.href = "/main";
                }
            });
        event.preventDefault();
    }

    login(user) {
        const cookies = new Cookies();
        cookies.set('user', user, { path: '/' });
        cookies.set('user', user, { path: '/main'});
        console.log(cookies.get('user'));
    }


    render() {
        return (
            <main style={{flex:"1 0 auto"}}>
                <div className="section"></div>
                <h5 className="indigo-text center">Please login into your account</h5>
                <div className="section"></div>

                <div className="container" style={{display: 'flex', justifyContent: 'center'}}>
                    <div className="z-depth-1 grey lighten-4 row" style={divStyle}>
                        <form onSubmit={this.handleSubmit}>
                            <Row>
                                <Input s={12} id="inputUN" label="Username" onChange={this.handleChange}/>
                            </Row>
                            <Row>
                                <Input s={12} id="inputPW" ref={x => this.inputPW=x} type="Password" label="Password" onChange={this.handleChange}/>
                                <label style={{float:"right"}}>
                                    <a className="pink-text"><Link to="/resetpw"> Forgot password?</Link></a>
                                </label>

                            </Row>
                            <Row>
                                <div className="center">
                                    <Button s={12} waves='light' className="purple btn-large col s12" type="submit" value="submit">LOGIN</Button>
                                </div>
                            </Row>
                        </form>
                    </div>
                </div>
                <div className="center">
                    <a href="/signup">Not Registered? Create Account</a>
                </div>

            </main>

        );
    }

}
export default Signon;


