import React, { Component } from 'react';
import CryptoJS from 'crypto-js';
import Cookies from 'universal-cookie';

const cookies = new Cookies();
class LikeButton extends React.Component {
    render() {
        const label = this.props.liked ? 'unlike' : ' like ';
        return (
            <button className="likeBtn" onClick={()=>this.props.onClick()}>
                {label}
            </button>
        );
    }
}

class BusList extends Component{
    constructor(props){
        super(props);
        // this.userId = props.userId;
        this.state = {bus: {}, liked:{}};
        this.userId = cookies.get("user");
        // this.userId = "bv2nCi5Qv5vroFiqKGopiw";
    }
    componentDidMount(){
        fetch("/bus/favByUserId?userId=" + this.userId)
            .then(  res => res.json()  )
            .then(  res => {
                const bus = {}, liked={};
                for(let i=0; i<res.length; i++){
                    bus[res[i].id] = res[i];
                    liked[res[i].id] = true;
                }
                this.setState({bus, liked});
                console.log(this.state.bus);
                console.log(this.state.liked);
            }  )
            .then(  () => {
                let arr = Object.entries(this.state.bus);
                for (let i=0; i<arr.length; i++){
                    console.log(arr[i][0]);
                    let obj = arr[i][1];
                    let pos = {lat: obj['latitude'], lng: obj['longitude']};
                    let ret = this.props.addNewPin(pos, String.fromCharCode(65+i));
                }
            });
    }
    renderTableHead(){
        return (<tr>
            <th>Label</th>
            <th>name, user: {this.userId}</th>
            <th>liked</th>
        </tr>);
    }
    handleAddBusClick(){
        const busl = this.state.bus, likedl = this.state.liked;
        const newb = this.props.newBusPin;
        let name = "test_"+Date.now();
        let hash = CryptoJS.MD5(name).toString(CryptoJS.enc.Base64).slice(0,22);
        console.log("newbus hash="+hash);
        likedl[hash] = true;
        busl[hash] = {id:hash, name:name, latitude:newb.lat(), longitude:newb.lng(),
        city:'unknown', state:'unknown'};
        this.setState({busl});
        this.setState({liked:likedl});
        fetch("/bus/insFavBus",{
            method: "POST",
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({userid: this.userId, busid: hash})
        });
        fetch("/bus/insRawBus",{
            method: "POST",
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(busl[hash])
        });
    }
    handleFavClick(key){
        const newliked = this.state.liked;
        newliked[key] = !newliked[key];
        this.setState({liked:newliked});
        if(newliked[key]){
            fetch("/bus/insFavBus",{
                method: "POST",
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({userid: this.userId, busid: key})
            });
        }
        else{
            fetch("/bus/delFavBus",{
                method: "POST",
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({userid: this.userId, busid: key})
            });
        }
    }
    render() {
        const busl = this.state.bus, likedl = this.state.liked;
        console.log("busList rendered " + Date.now());
        return (
            <div>
                <table>
                    <thead>
                    {this.renderTableHead()}
                    </thead>
                    <tbody>
                    {Object.keys(busl).map( (key, index) =>
                        // this.renderTableRow(key)
                        <tr key={key}>
                            <td>{String.fromCharCode(65 + index)}</td>
                            <td>{busl[key].name}</td>
                            <td>
                                <LikeButton
                                    liked={likedl[key]}
                                    onClick={() => this.handleFavClick(key)}
                                />
                            </td>
                        </tr>
                    )}
                    <tr>
                        <td></td>
                        <td></td>
                        <td><button className="likeBtn" onClick={()=>this.handleAddBusClick()}>
                            add
                        </button></td>
                    </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default BusList;



