import React, { Component } from 'react';
import {Row, Input, Button, Icon} from 'react-materialize';
class Insert extends Component {
    constructor(props) {
        super(props);
        this.state = {id: "", name: "", city: "", state: "", latitude: 0.0, longitude: 0.0};

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        switch (event.target.id) {
            case "inputUN":
                this.state.name = event.target.value;
                break;
            case "inputNN":
                this.state.city = event.target.value;
                break;
            case "inputRN":
                this.state.state = event.target.value;
                break;
            case "inputEmail":
                this.state.latitude = parseFloat(event.target.value);
                break;
            case "inputPhone":
                this.state.longitude= parseFloat(event.target.value);
                break;
            case "inputID":
                this.state.id = event.target.value;
                break;
        }
        // this.setState({value: event.target.value});
    }

    handleSubmit(event) {
        console.log(this.state);
        fetch("/users/update", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state)
        });
        // console.log(this.state);
        event.preventDefault();
    }
    render() {
        return (
            <div class="container">
                <form onSubmit={this.handleSubmit}>
                    <Row>
                        <Input s={6} id="inputID" label="id" onChange={this.handleChange}/>
                        <Input s={6} id="inputUN" label="name" onChange={this.handleChange}/>
                        <Input s={6} id="inputNN" label="city" onChange={this.handleChange}/>
                        <Input s={6} id="inputRN" label="state" onChange={this.handleChange}/>
                        <Input s={6} id="inputEmail" label="latitude" onChange={this.handleChange}/>
                        <Input s={6} id="inputPhone" label="longitude" onChange={this.handleChange}/>
                        <Button waves='light' type="submit" value="submit">Submit</Button>
                    </Row>
                </form>
            </div>
        );
    }
}
export default Insert;


