import React, { Component } from 'react';
import {Row, Input, Button, nav} from 'react-materialize';
var divStyle = {
    display: "inline-block",
    padding: "32px 48px 0px 48px",
    border: "1px solid #EEE",
    height: "750px",
    width: "349px"
};
var iconStyle = {
    display: "inline-block",
    margin:"24px 10px 0px",
    height:"0px",
    width:"0px"

};
class Resetpw extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: "",
            code: "",
            phone: "",
            pwType:"password",
            password:"",
            codeButton:true,
            pwButton: true,
            phoneButton: false,
            disableUN: "",
            disablePhone: "",
            disableCode: ""

        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleSubmitCode = this.handleSubmitCode.bind(this);
        this.handleSubmitPW = this.handleSubmitPW.bind(this);
        this.handleMouseDown = this.handleMouseDown.bind(this);

        // this.inputPW = React.cr;
    }

    handleChange(event) {
        switch (event.target.id) {
            case "inputUN":
                this.state.username = event.target.value;
                break;
            case "inputPhone":
                this.state.phone = "+1"+event.target.value;
                break;
            case "inputCode":
                this.state.code = event.target.value;
                break;
            case "inputPW":
                this.state.password = event.target.value;
                break;
        }
    }


    handleMouseDown(event) {
        event.preventDefault();
        event.stopPropagation();
        // alert("click icon");
        this.setState({
            pwType: this.state.pwType === "text" ? "password" : "text"
        });

    }

    handleSubmitPW(event) {
        event.preventDefault();
        fetch("/credentials/resetpwpw", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify(this.state)
        }).then(function() {
            window.location.href = "/";
        });
    }

    handleSubmitCode(event) {
        fetch("/credentials/resetpwcode", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify(this.state)
        }).then(res => res.json())
            .then(data => {
                console.log(data[0].correct);
                if (!data[0].correct) {
                    alert("Code does not match, please try again");
                    // console.log(this.inputPW.value);
                    // this.inputPW.value = "";
                } else {
                    this.setState({pwButton: false, disableCode:"disabled", codeButton: true});
                }
            });
        // event.preventDefault();
        event.preventDefault();
    }

    handleSubmit(event) {
        console.log(this.state);
        fetch("/credentials/resetpw", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify(this.state)
        }).then(res => res.json())
            .then(data => {
                console.log(data[0].correct);
                if (!data[0].correct) {
                    alert("Username and phone does not match, please try again");
                    // console.log(this.inputPW.value);
                    // this.inputPW.value = "";
                } else {
                    this.setState({codeButton: false, disablePhone:"disabled", disableUN:"disabled", phoneButton:true});
                }
            });
        event.preventDefault();
    }

    render() {
        return (
            <main style={{flex:"1 0 auto"}}>
                <div className="section"></div>
                <h5 className="indigo-text center">Forget your password? We need your username and phone.</h5>
                <div className="section"></div>

                <div className="container" style={{display: 'flex', justifyContent: 'center'}}>
                    <div className="z-depth-1 grey lighten-4 row" style={divStyle}>
                        <form onSubmit={this.handleSubmit}>
                            <Row>
                                <Input s={12} id="inputUN" label="Username" onChange={this.handleChange} disabled={this.state.disableUN}/>
                            </Row>
                            <Row>
                                <Input s={12} id="inputPhone" type="tel" pattern="[0-9]{10}" label="Phone" validate={true} required aria-label="Please input username." aria-required="true" onChange={this.handleChange} disabled={this.state.disablePhone}/>
                            </Row>
                            <Row>
                                <div className="center">
                                    <Button s={12} waves='light' className="purple btn-large col s12" type="submit" value="submit" disabled={this.state.phoneButton}>Get Code</Button>
                                </div>
                            </Row>
                        </form>

                        <form onSubmit={this.handleSubmitCode}>
                            <Row>
                                <Input s={12} id="inputCode" label="Verification Code" onChange={this.handleChange} disabled={this.state.disableCode}/>
                            </Row>
                            <Row>
                                <div className="center">
                                    <Button s={12} waves='light' className="purple btn-large col s12" type="submit" value="submit" disabled={this.state.codeButton}>Submit Code</Button>
                                </div>
                            </Row>
                        </form>

                        <form onSubmit={this.handleSubmitPW}>
                            <Row>
                                <Input s={10} m={10} l={10} id="inputPW" type={this.state.pwType} validate={true} required aria-label="Please input username." aria-required="true" label="Password" onChange={this.handleChange}/>
                                <div style={iconStyle}>
                                    <Button l={1} floating type="button" onMouseUp={this.handleMouseDown} onMouseDown={this.handleMouseDown} icon="remove_red_eye"></Button>
                                </div>
                            </Row>
                            <Row>
                                <div className="center">
                                    <Button s={12} waves='light' className="purple btn-large col s12" type="submit" value="submit" disabled={this.state.pwButton}>Reset Password</Button>
                                </div>
                            </Row>
                        </form>
                    </div>
                </div>

            </main>

        );
    }

}
export default Resetpw;


