import React, { Component } from 'react';
import {Row, Input, Button, Icon} from 'react-materialize';
var divStyle = {
    display: "inline-block",
    padding: "32px 48px 0px 48px",
    border: "1px solid #EEE",
    height: "425px",
    width: "349px"
};
var iconStyle = {
    display: "inline-block",
    margin:"24px 10px 0px",
    height:"0px",
    width:"0px"

};
class Signup extends Component {
    constructor(props) {
        super(props);
        this.state = {username: "", password: "", pwType: "password", phone: ""};
        this.handleChange = this.handleChange.bind(this);
        this.handleMouseDown = this.handleMouseDown.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);

    }

    handleChange(event) {
        switch (event.target.id) {
            case "inputUN":
                this.state.username = event.target.value;
                break;
            case "inputPhone":
                this.state.phone = "+1"+event.target.value;
                console.log(this.state.phone);
                break;
            case "inputPW":
                this.state.password = event.target.value;
                break;

        }
    }

    handleMouseDown(event) {
        event.preventDefault();
        event.stopPropagation();
        // alert("click icon");
        this.setState({
            pwType: this.state.pwType === "text" ? "password" : "text"
        });

    }




    handleSubmit(event) {
        event.preventDefault();
        console.log(this.state);
        fetch("/credentials/create", {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            credentials: 'include',
            body: JSON.stringify(this.state)
        }).then(function() {
            window.location.href = "/";
        });
    }


    render () {
        return (

            <main style={{flex:"1 0 auto"}}>
                <div className="section"></div>
                <h5 className="indigo-text center">Please create your user account</h5>
                <div className="section"></div>

                <div className="container" style={{display: 'flex', justifyContent: 'center'}}>
                    <div className="z-depth-1 grey lighten-4 row" style={divStyle}>
                        <form onSubmit={this.handleSubmit}>
                            <Row>
                                <Input s={12} id="inputUN" label="Username" validate={true} required aria-label="Please input username." aria-required="true" onChange={this.handleChange}/>
                            </Row>
                            <Row>
                                <Input s={12} id="inputPhone" type="tel" pattern="[0-9]{10}" label="Phone" validate={true} required aria-label="Please input username." aria-required="true" onChange={this.handleChange}/>
                            </Row>
                            <Row>
                                <Input s={10} m={10} l={10} id="inputPW" type={this.state.pwType} validate={true} required aria-label="Please input username." aria-required="true" label="Password" onChange={this.handleChange}/>
                                <div style={iconStyle}>
                                    <Button l={1} floating type="button" onMouseUp={this.handleMouseDown} onMouseDown={this.handleMouseDown} icon="remove_red_eye"></Button>
                                </div>


                            </Row>

                            <Row>
                                <div className="center">
                                    <Button s={12} waves='light' className="purple btn-large col s12" type="submit" value="submit">Register!</Button>
                                </div>
                            </Row>
                        </form>
                    </div>
                </div>


            </main>


        );
    }
}
export default Signup;


